nav {
    background-color: var(--blue);
}

body {
    min-width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

main {
    flex-grow: 1;
    flex-basis: 0;
    overflow-y: auto;
    background-color: var(--blue);
    padding-left: var(--x-padding);
    padding-right: var(--x-padding);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* justify-content: center;
    align-items: center; */
    width: 100%;
}

.hidden { display: none; }

#form-content-box {
    margin-bottom: 1vh;
    place-self: center;
    width: 650px;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 1vh;
    width: 650px;
    place-self: center;
    font-family: "Work Sans";
    color: white;
}

.form-group {
    display: flex;
    flex-direction: column;
    gap: 0.3vh;
}

#submit {
    margin-top: 2vh;
    font-family: "Work Sans";
    padding: 7px;
    border-radius: 100px;
    border: none;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
    #form-content-box {
        place-self: unset;
        width: unset;
        max-width: 650px;
    }

    form {
        place-self: unset;
        width: unset;
        max-width: 650px;
    }
}

header h1 {
    font-size: clamp(2rem, 7vw, 3rem);
    margin-top: 1em;
}

header p {
    font-size: clamp(1.2rem, 3vw, 1.3rem);
    font-weight: 400;
    margin-top: 1.5vh; 
    margin-bottom: 3vh;
}

p {
    font-size: clamp(1.2rem, 3vw, 1.2rem);
}

.footer p {
    font-size: clamp(1.2rem, 5vw, 2rem);
    font-weight: 600;
}

.stripes {
    margin-top: 24px;
    margin-bottom: 24px;
    height: 72px;
    background-color: white;

    background: url(./stripes.svg);

    /*
      This is a little convoluted: The stipes are located as <div> elements
      inside each <section> tag. The section tags have padding (--x-padding)
      but we want the stripes to run the full width of the section and ignore
      the padding. Bellow is how that is accomplished..
    */
    width: 100vw;
    margin-left: calc(0px - var(--x-padding));
}
