nav {
  background-color: var(--blue);
}

body {
  flex-direction: column;
  min-width: 100vw;
  height: 100vh;
  display: flex;
}

main {
  background-color: var(--blue);
  padding-left: var(--x-padding);
  padding-right: var(--x-padding);
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  justify-content: space-between;
  width: 100%;
  display: flex;
  overflow-y: auto;
}

.hidden {
  display: none;
}

#form-content-box {
  place-self: center;
  width: 650px;
  margin-bottom: 1vh;
}

form {
  color: #fff;
  flex-direction: column;
  justify-content: space-evenly;
  place-self: center;
  gap: 1vh;
  width: 650px;
  font-family: Work Sans;
  display: flex;
}

.form-group {
  flex-direction: column;
  gap: .3vh;
  display: flex;
}

#submit {
  border: none;
  border-radius: 100px;
  margin-top: 2vh;
  padding: 7px;
  font-family: Work Sans;
}

@media screen and (width <= 700px) and (orientation: portrait) {
  #form-content-box, form {
    place-self: unset;
    width: unset;
    max-width: 650px;
  }
}

header h1 {
  margin-top: 1em;
  font-size: max(2rem, min(7vw, 3rem));
}

header p {
  margin-top: 1.5vh;
  margin-bottom: 3vh;
  font-size: max(1.2rem, min(3vw, 1.3rem));
  font-weight: 400;
}

p {
  font-size: max(1.2rem, min(3vw, 1.2rem));
}

.footer p {
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

.stripes {
  width: 100vw;
  height: 72px;
  margin-top: 24px;
  margin-bottom: 24px;
  margin-left: calc(0px - var(--x-padding));
  background: url("stripes.c032f86a.svg");
}
